import api from '@/common/api.js'
import { Config } from '@/config/config.js'
export default {
	data() {
		return {
			phone: '',
			pwd: ''
		}
	},
	methods: {
		onPhoneInput(event) {
			this.phone = event.target.value;
		},
		onPwdInput(event) {
			this.pwd = event.target.value;
		},
		check() {
			if (this.phone == '') {
				uni.showToast({
					title: '请输入手机号',
					duration: 2000
				});
				return false;
			} else if (this.pwd == '') {
				uni.showToast({
					title: '请输入密码',
					duration: 2000
				});
				return false;
			}
			return true;
		},
		login() {
			if (!this.check()) return;
			let params = {
					phone: this.phone,
					password: this.pwd
			};
			api.login(params).then((result)=>{
				if (result.code == 0) {
					getApp().globalData.token = result.data.token;
					try {
					    uni.setStorageSync('token', result.data.token);
					} catch (e) {
					    // error
					}  
					let pages = getCurrentPages();
					let prevPage = pages[pages.length - 2];
					prevPage.onLoad();
					uni.navigateBack();
				} else {
					uni.showToast({
						title: result.msg,
						duration: 2000
					});
				}
				// uni.switchTab({
				// 	url: '/pages/account/Account',
				// 	success(e) {
				// 		var page = getCurrentPages().pop();
				// 		if (page == undefined || page == null) return;
				// 		page.onLoad();
				// 	}
				// })
			});
		}
	}
}
